import { Toast } from 'vant'


class MTToast{
  success(message){
    Toast.success(message ? message : '')
  }

  fail(message){
    Toast.fail(message ? message : '')
  }

  text(message){
    Toast.text(message)
  }

  loading(message){
    Toast.loading(message ? message : '...')
  }

  close(){
    Toast.clear()
  }
}

export default new MTToast()